                            <img v-if="screen == index+1" :src="axios.defaults.baseURL + '/' + image_url" alt="" class="img-fluid rounded">                            
                          </transition>

                          <font-awesome-icon class="text-dark fw-bold right-circle bg-white bg-gradient" :icon="['fas', 'arrow-right']" v-if="screen != no_images" @click="nextScreen()"></font-awesome-icon> 

                          <font-awesome-icon class="text-dark fw-bold left-circle bg-white bg-gradient" :icon="['fas', 'arrow-left']" v-if="screen != 1" @click="previousScreen()"></font-awesome-icon>
                        </div>
                        
                        <div class="d-flex mt-2">
                           <img v-for="({image_url}, index) in property.property_images" :key="index" :src="axios.defaults.baseURL + '/' + image_url" alt="" class="img-fluid rounded me-3" @click="changeScreen(index+1)" style="width: 8%; height: 10%; cursor: pointer">{{ index }}
                        </div>
                        
                        <div class="d-flex">
                          <figcaption class="ms-auto me-1 mt-2">{{ caption }}</figcaption>
                        </div>
                        
                        <div class="mt-4">
