                              <p class="mt-2 mb-3" style="font-size: 17px;"> 
                                <font-awesome-icon class="me-3 text-success" style="font-size: 14px" :icon="['fas', 'money-bill']"></font-awesome-icon> 
                                <span v-if="property.price" class="fw-bold">Ksh. {{ formatNumber(property.price) }} </span> 
                              </p>
                            </div>

                            <div class="col-md-6 col-sm-6">
                              <p class="mt-2 mb-3" style="font-size: 17px;"> 
                                <font-awesome-icon class="me-2 text-success" style="font-size: 14px" :icon="['fas', 'bed ']"></font-awesome-icon> 
                                <span class="text-capitalize fw-bold">{{ property.bedrooms }} Bedrooms</span>
                              </p>

                              <p class="mt-2 mb-3" style="font-size: 17px;"> 
                                <font-awesome-icon class="me-2 text-success" style="font-size: 14px" :icon="['fas', 'shower']"></font-awesome-icon> 
                                <span class="text-capitalize fw-bold">{{ property.bathrooms }} bathrooms</span>
                              </p>
                            </div>                        
                          </div>
                        </div>                    

                        <div class="mt-4">
                          <h5 class="text-dark mb-1 mt-2 fw-bold">Amenities</h5>
                          
                          <div class="row" >

                            <div v-for="({amenity}, index) in property.amenities" :key="index" class="col-md-3 col-sm-6 mt-4">
                              <div class="card bg-light p-2 pt-3 pb-3">
                                <h4 class="text-center">
                                <font-awesome-icon :icon="['fas', amenity_icons[amenity]]"></font-awesome-icon>
                                </h4>
                                <p class="text-center mb-0 mt-2">{{ amenity }}</p>
                              </div>
                            </div>
                          </div>
                        </div>
                    </div>

                    <div class="col-md-4 mt-4">
                      <div class="card bg-light">
                        <div class="card-body">
                          <h4 class="text-dark">Interested in this property?</h4>
                          <small class="text-muted">Get in touch today</small>

                          <form action="#" @submit.prevent="handleSubmit()">

                            <status v-model:status_data=status_data></status>

                            <div class="form-row mt-4">
                              <div class="input-group">
                                  <span class="input-group-text border-end-0 bg-white" style="border: 1px solid #eee">
                                      <font-awesome-icon class="ms-2" id="eye" :icon="['fa', 'user']" />
                                  </span>

                                  <input v-model="form_data.names" id="names" type="text" class="form-control border-start-0" placeholder="Enter your Full Names" required> 
                              </div>
                            </div>

                            <div class="form-row mt-4">
                              <div class="input-group">
                                  <span class="input-group-text border-end-0 bg-white" style="border: 1px solid #eee">
                                      <font-awesome-icon class="ms-2" id="eye" :icon="['fa', 'envelope']" />
                                  </span>

                                  <input v-model="form_data.email" id="email" type="email" class="form-control border-start-0" placeholder="Enter your Email." required> 
                              </div>
                            </div> 

                            <div class="form-row mt-4">
                              <div class="input-group">
                                  <span class="input-group-text border-end-0 bg-white" style="border: 1px solid #eee">
                                      <font-awesome-icon class="ms-2" id="eye" :icon="['fas', 'phone-alt']" />
                                  </span>

                                  <input v-model="form_data.phone_number" id="phone_number" type="number" class="form-control border-start-0" placeholder="Enter your Phone Number" required> 
                              </div>
                            </div>

                            <div class="form-row mt-4">
                              <div class="input-group">
                                  <span class="input-group-text border-end-0 bg-white" style="border: 1px solid #eee">
                                      <font-awesome-icon class="ms-2" id="eye" :icon="['fas', 'phone-alt']" />
                                  </span>

                                  <select v-model="form_data.purpose" name="purpose" id="purpose" class="form-select border-start-0">
                                    <option>Book a tour or show interest in property</option>
                                    <option value="tour">Book House Tour</option>
                                    <option value="purchase">Interested in Purchase</option>
                                  </select>
                              </div>
                            </div>                          

                            <div class="form-row mt-4">
                              <div class="input-group">
                                  <span class="input-group-text border-end-0 bg-white" style="border: 1px solid #eee">
                                      <font-awesome-icon class="ms-2" id="eye" :icon="['fas', 'comment']" />
                                  </span>

                                  <textarea v-model="form_data.description"  style="line-height: 1.5" placeholder="You can write us a message here if you want to." class="form-control border-start-0" name="description" id="description" rows="7"></textarea>
                              </div>
                              <small class="text-muted">Optional field</small>
                            </div>   

                            <button type="submit" class="btn btn-success btn-lg w-100 mt-4">
                              <span class="text-uppercase">Submit</span>
                              <font-awesome-icon class="ms-3" :icon="['fas', 'angle-right']"></font-awesome-icon>
                            </button>   

                          </form>                                                                    
                        </div>
                      </div>
                    </div>
