</style>

<script>
  import Breadcrumbs from '../../components/AutoBreadcrumbs'
  import Navbar from '../../components/Custom/PropertyNavbar'
  import MainFooter from '../../components/Footer'
  import Status from '../../components/Status'

  export default {
    name: 'Home',
    props: ['property_id'],
    components: {
      Navbar,
      Breadcrumbs,
      MainFooter,
      Status
    }, 
    computed: {
      page_props () {
        return this.$store.state.page_props
      }
    },      
    data() {
      return {
        title: {status: true, title: 'Your Data', actions: true},
        breadcrumbs_data: [
          {title: 'Home', link: '', active: false},
          {title: 'Property', active: true},
        ],
        screen: 1,
        no_images: null,
        caption: null,
        property: {},
        amenity_icons: {
          'Wi-Fi': 'wifi',
          'CCTV': 'video',
          'Gym': 'dumbbell',
          'Swimming Pool': 'swimming-pool',
          '24 Hour Security': 'lock',
          'Parking Spots': 'parking',
          'Outdoor Space': 'spa',
          'Elevator': 'sort',
          'Generator': 'bolt',
          'Borehole': 'hand-holding-water',

        },
        form_data: {
          names: null,
          email: null,
          purpose: null,
          phone_number: null,
          description: null,
          property: null
        },
        status_data: {
          "success": false,
          "success_message": 'Form Submission was successful!',
          "error": false,
          "error_message": 'There are some errors.',
          "loading": false
        },        
      } 
    },
    mounted() {
      if(this.page_props.length == 0) {
        this.$store.dispatch("getProps");
      }

      this.getProperty()
    },
    methods: {
      handleSubmit() {
        this.status_data.loading = true
        this.form_data.property = {
          name: this.property.name,
          id: this.property.id
        }

        this.axios.post('api/message/', this.form_data).then(() => {
          this.status_data.loading = false
          this.status_data.success = true
          this.status_data.error = false
          this.status_data.success_message = 'Success!'
        }).catch((error) => {
          this.status_data.loading = false
          this.status_data.success = false
          this.status_data.error = true
          this.status_data.error_message = 'Error! ' + error.response.data.message

        })
      },
      nextScreen() {
        this.screen++
        this.caption = this.property.property_images[this.screen-1].caption
      },
      previousScreen() {
        this.screen--
        this.caption = this.property.property_images[this.screen-1].caption
      },
      changeScreen(value) {
        this.screen = value
      },
      formatNumber(num) {
        return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
      },
      getProperty() {
        this.axios.get('api/property/' + this.property_id).then((response) => {
          console.log(response);
          this.property = response.data.data
          this.no_images = this.property.property_images.length
          
          if(this.no_images > 0) {
            this.caption = this.property.property_images[this.screen-1].caption
          }
        }).catch(() => {
          this.$router.push({name: 'Properties'});
        })
      }
    }
  }
</script>
